import { useEffect, useState } from "react";

export default Child = ({ parentFunction }) => {
  console.log("Child loaded.");

  // Get function from parent.
  const [num, setNum] = useState(0); // Child's data setting.

  useEffect(() => {
    // Use useEffect to aync the value
    parentFunction(num); // Pass the value back through parent's function.
    console.log("Child num: ", num); // Print value and check in console.
  }, [num]);

  const childFunction = () => {
    // Button onclick function.
    setNum(num + 1); // Just change value simply.
  };

  return <button onClick={() => childFunction()}>Child button: {num}</button>;
  // Use button onclick to change value.
};
