import "./styles.css";
import { useState } from "react";
import Child from "./Child"; // Import child component.

export default Parent = () => {
  const [get, setGet] = useState(""); // Parent's data setting.
  const parentFunction = (data) => {
    setGet(data); // Set value from child component.
    console.log("Function get data: ", data); // The value function get from child.
  };
  console.log("Parent data: ", get); // Print value and check in console.

  console.log("//////////////////////");

  return (
    <div className="App">
      <h1>React child to parent example.</h1>
      <h2>Click the button below and get value from child.</h2>
      <h2>Parent data: {get}</h2>
      {/* Print result. */}

      <Child parentFunction={parentFunction} />
      {/* Pass function to child component. */}
      {/* Also get value from this function. */}
    </div>
  );
};
